*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

body {
  color: var(--primary-color);
  letter-spacing: .04em;
  height: 100%;
  background-color: #fff;
  background-image: url("ice_cream.712e2b6b.jpg");
  background-position: 0 0;
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  font-family: Montserrat;
  font-size: 14px;
  line-height: 2;
  text-decoration: none;
}

@media screen and (min-device-pixel-ratio: 2), screen and (min-resolution: 192dpi), screen and (min-resolution: 2x) {
  body {
    background-image: url("ice_cream@2x.541b87bc.jpg");
  }
}

img {
  height: auto;
  max-width: 100%;
  display: block;
}

ul, li {
  margin: 0;
  padding: 0;
  list-style: none;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
}

fieldset {
  border: 0;
  margin: 0;
  padding: 0;
}

button {
  cursor: pointer;
  border: none;
  margin: 0;
  padding: 0;
  line-height: 0;
}

.container {
  margin: 0 auto;
  padding: 0 20px;
}

@media screen and (min-width: 480px) {
  .container {
    max-width: 480px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
    width: 768px;
    padding: 0 32px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    max-width: 1200px;
    width: 1200px;
    padding: 0 16px;
  }
}

:root {
  --primary-color: #fcfcfc;
  --secondary-color: #131313;
  --active-color: #f8d24e;
  --active-color-shadow: rgba(248, 210, 78, .7);
  --bg-color: #84a178;
  --btn-hover-focus--color: #0a8817;
  --btn-disabled-color: #ababab;
}

.overtext {
  text-align: center;
  text-transform: uppercase;
  margin-bottom: 12px;
  font-family: Montserrat;
  font-size: 30px;
  font-weight: 800;
  line-height: 1.23;
}

.title {
  text-align: center;
  letter-spacing: .06em;
  text-transform: uppercase;
  font-family: Seymour One;
  font-size: 36px;
  font-weight: 400;
  line-height: 1.25;
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.mobile-menu-btn {
  background-color: var(--bg-color);
  border: 0;
  padding: 0;
  line-height: 0;
  display: block;
}

.mobile-menu-btn__icon {
  width: 32px;
  height: 32px;
  fill: #fff;
}

.button {
  min-height: 42px;
  text-align: center;
  color: var(--secondary-color);
  border: 0;
  border-radius: 10px;
  align-items: center;
  padding: 12px 34px;
  font-size: 14px;
  font-weight: 600;
  line-height: 18px;
}

.button-arrow__icon {
  width: 12px;
  height: 12px;
  fill: var(--secondary-color);
  stroke: var(--secondary-color);
  stroke-width: 4px;
}

.btn-solid-bg:focus .button-arrow__icon, .btn-franchise:focus .button-arrow__icon {
  fill: var(--primary-color);
  stroke: var(--primary-color);
}

.btn-solid-bg {
  background: var(--active-color);
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1), color .25s cubic-bezier(.4, 0, .2, 1);
}

.btn-solid-bg:hover {
  background-color: var(--btn-hover-focus--color);
}

.btn-solid-bg:focus {
  background: var(--btn-hover-focus--color);
  color: var(--primary-color);
}

.btn-solid-bg:disabled {
  background-color: var(--btn-disabled-color);
}

.btn-products {
  min-width: 62px;
  min-height: 24px;
  background: var(--bg-color);
  text-align: center;
  color: var(--secondary-color);
  border: 0;
  border: 2px solid var(--active-color);
  color: var(--secondary-color);
  border-radius: 8px;
  align-items: center;
  padding: 8px 10px;
  font-size: 8px;
  font-weight: 600;
  line-height: 10px;
  transition: border-color .25s cubic-bezier(.4, 0, .2, 1), color .25s cubic-bezier(.4, 0, .2, 1);
}

.btn-products:hover {
  border-color: var(--btn-hover-focus--color);
}

.btn-products:focus {
  border-color: var(--btn-hover-focus--color);
  color: var(--primary-color);
}

.btn-products:disabled {
  border-color: var(--btn-disabled-color);
}

.btn-hero {
  min-width: 82px;
  color: var(--active-color);
  background-color: var(--bg-color);
  border: 0;
  padding: 8px 10px;
  font-size: 8px;
  font-weight: 600;
  line-height: 10px;
  text-decoration: underline;
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
}

.btn-hero:hover {
  color: var(--btn-hover-focus--color);
}

.btn-hero:focus {
  color: var(--primary-color);
}

.btn-hero:disabled {
  color: var(--btn-disabled-color);
}

.btn-modal-close {
  background-color: var(--primary-color);
  border: 0;
  padding: 0;
  line-height: 0;
}

.btn-modal-close__icon {
  width: 16px;
  height: 16px;
  fill: var(--secondary-color);
}

.btn-modal {
  min-height: 42px;
  text-align: center;
  color: var(--secondary-color);
  background: var(--active-color);
  border: 0;
  border-radius: 10px;
  align-items: center;
  padding: 12px 34px;
  font-size: 12px;
  font-weight: 600;
  line-height: 15px;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1), color .25s cubic-bezier(.4, 0, .2, 1);
}

.btn-modal:hover {
  background-color: var(--btn-hover-focus--color);
}

.btn-modal:focus {
  background-color: var(--btn-hover-focus--color);
  color: var(--primary-color);
}

.btn-modal:disabled {
  background-color: var(--btn-disabled-color);
}

.btn-open-modal-products {
  width: 100%;
  background-color: rgba(0, 0, 0, 0);
  border: 0;
  justify-content: center;
  margin-top: auto;
  padding-bottom: 12px;
  display: flex;
}

.open-modal-products__icon {
  width: 40px;
  height: 16px;
  fill: var(--secondary-color);
  transition: width .25s cubic-bezier(.4, 0, .2, 1), stroke-width .25s cubic-bezier(.4, 0, .2, 1);
}

.btn-open-modal-products:hover .open-modal-products__icon {
  width: 50px;
}

.btn-open-modal-products:focus .open-modal-products__icon {
  width: 50px;
  height: 22px;
  stroke: var(--secondary-color);
  stroke-width: 1px;
}

.btn-franchise {
  min-width: 196px;
  min-height: 42px;
  background: var(--bg-color);
  text-align: center;
  color: var(--secondary-color);
  border: 2px solid var(--active-color);
  color: var(--secondary-color);
  border-radius: 8px;
  align-items: center;
  padding: 12px 46px;
  font-size: 14px;
  font-weight: 600;
  line-height: 17px;
  transition: border-color .25s cubic-bezier(.4, 0, .2, 1), color .25s cubic-bezier(.4, 0, .2, 1);
}

.btn-franchise:hover {
  border-color: var(--btn-hover-focus--color);
}

.btn-franchise:focus {
  border-color: var(--btn-hover-focus--color);
  color: var(--primary-color);
}

.btn-franchise:disabled {
  border-color: var(--btn-hover-focus--color);
}

@media screen and (min-width: 1200px) {
  .open-mobile-menu-btn {
    display: none;
  }

  .btn-products {
    min-width: 134px;
    min-height: 42px;
    padding: 12px 34px;
    font-size: 14px;
    line-height: 18px;
  }

  .btn-hero {
    min-width: 154px;
    min-height: 40px;
    padding: 12px 22px;
    font-size: 14px;
    line-height: 17px;
  }

  .btn-modal {
    font-size: 14px;
    line-height: 17px;
  }

  .btn-open-modal-products {
    padding-bottom: 52px;
  }

  .btn-modal-close__icon {
    width: 20px;
    height: 20px;
  }
}

.backdrop {
  z-index: 200;
  width: 100%;
  height: 100%;
  opacity: 1;
  visibility: visible;
  pointer-events: auto;
  background-color: rgba(0, 0, 0, .2);
  padding-left: 15px;
  padding-right: 15px;
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: scroll;
}

.backdrop.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.modal-input {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
  display: flex;
}

.modal-input__name-m, .modal-input__phone-m, .modal-input__email-m {
  width: 224px;
  height: 32px;
  background: rgba(248, 210, 78, .1);
  border: 1px solid #84a178;
  border-radius: 10px;
  margin-bottom: 12px;
  padding-top: 8px;
  padding-bottom: 8px;
  padding-left: 12px;
}

.modal-input__name-ing-m, .modal-input__phone-ing-m {
  width: 240px;
  height: 42px;
  background: rgba(248, 210, 78, .1);
