  padding-left: 12px;
}

.submit-button {
  width: 114px;
  height: 42px;
  cursor: pointer;
  background: #f8d24e;
  border-radius: 10px;
  justify-content: center;
  align-items: center;
  margin-top: 24px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
}

@media screen and (max-width: 767px) {
  .modal-video {
    z-index: 200;
    width: 280px;
    height: 459px;
    background: #fcfcfc;
    border: 2px solid #f8d24e;
    border-radius: 10px;
    margin-top: 20px;
    transition: opacity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1);
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
  }

  .modal-video__how {
    text-align: center;
    color: #131313;
    margin-top: 28px;
    margin-bottom: 20px;
    font-family: Seymour One;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 1.5;
  }

  .modal-video__play {
    width: 224px;
    height: 199px;
    background-image: url("ice-cream.95dd595a.jpg");
    background-size: cover;
    border-radius: 10px;
    justify-content: center;
    align-items: center;
    margin-left: auto;
    margin-right: auto;
    display: flex;
  }

  .modal-video__text {
    color: #131313;
    padding: 20px 20px 28px;
    font-family: Montserrat;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 140%;
  }

  .video {
    animation-name: blinker;
    animation-duration: 1s;
    animation-timing-function: cubic-bezier(1, 0, 0, 1);
    animation-iteration-count: infinite;
  }

  @keyframes blinker {
    from {
      opacity: 1;
    }

    to {
      opacity: 0;
    }
  }

  .modal-close {
    cursor: pointer;
    background: none;
    border: none;
    margin: 8px;
    position: absolute;
    top: 0;
    right: 0;
  }
}

